/**
 *  Define scss variables here.
 *
 *  Available options for Bootstrap:
 *  http://getbootstrap.com/customize/
 *
 */
@font-face {
  font-family: 'Open Sans Condensed Bold';
  src: url("../../theme/Default/fonts/OpenSans-CondBold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans Condensed Light';
  src: url("../../theme/Default/fonts/OpenSans-CondLight.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans Condensed Light Italic';
  src: url("../../theme/Default/fonts/OpenSans-CondLightItalic.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.app .brand {
  position: absolute;
  top: 5px;
  left: 5px;
  display: inline-block;
  background: #2d2d2d url("../../static/logo.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 80%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.app nav :global(.fa) {
  font-size: 2em;
  line-height: 20px;
}

.appContent {
  margin-bottom: 10px;
  margin-top: 80px;
  text-align: center;
}



