.homeContainer {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px !important;
}

.dashboardGridContainer {
  width: 100%;
  max-width: 1200px !important;
}

@media screen and (min-width: 1200px) {
  .dashboardGridContainer {
    min-width: 1000px;
    max-width: 1200px !important;
  }
}

.dashboardCount {
  font-size: 48px;
  color: whitesmoke;
}

.dashboardCountContainer {
  background-color: lightblue;
  padding: 24px;
}

.dashboardCountTitle {
  background-color: black;
  color: white;
  margin: 0;
}



